    <section id="Cell_Atlas" class="home-section wg-about">
      <div class="container">
        <div class="row">
          <div class="col-12" >
            <h2 style="font-weight: bold; margin-bottom: 50px">A Comprehensive Developmental Cell Atlas for Zebrafish</h2>
            <div class="row">
              <div class="col-3">
                <h4 class="subtitle">What Is Cell Atlas?</h4>
              </div>
              <div class="col-9">
                <p class=""><span class="lead-emphasis">C</span>ell atlas is a way to illustrate <a href="https://www.ncbi.nlm.nih.gov/pmc/articles/PMC2918286" target="_blank" class="query">cellular heterogeneity</a> within an organism, cellular heterogeneity is a biological term which reflects distinct cell characters in different organ, or different development stage of an organ.</p>
                <p class="">Understanding of cellular heterogeneity has long been a core challenge for several biological subjects including Developmental Biology, Regenerative Medicine, Immunology, etc. And therefore has great influence on Precise Medicine, organ transplant and other biological engineering.</p>
              </div>
              <div style="width: 100%; height: 15px"></div>
              <div class="col-3">
                <h4  class="subtitle">Human Cell Atlas Consortium</h4>
              </div>
              <div class="col-9">
                <p class=""><span class="lead-emphasis">T</span>o promote the understanding of human cellular heterogeneity, there is a proposal of a convention, Human Cell Atlas (HCA, <a href="https://www.humancellatlas.org" target="_blank">www.humancellatlas.org</a>)<a class="citation-index" v-on:click="toReference()">(1-2)</a>. Experience from different labs around the world which comply to the convention can be utilized to build human cell atlas. Human Cell Atlas is another mega project after <a href="https://www.genome.gov/human-genome-project" target="_blank">The Human Genome Project</a>.</p>
              </div>
              <div style="width: 100%; height: 15px"></div>
              <div class="col-3">
                <h4  class="subtitle" >Single Cell RNA Sequencing</h4>
              </div>
              <div class="col-9">
                <p class=""><a href="https://en.wikipedia.org/wiki/Single-cell_transcriptomics" class="query" target="_blank"><span class="lead-emphasis">S</span>ingle cell RNA sequencing</a> (hereafter referred to as sc-RNA-seq) technology boosted since 2016, it is capable to dissect <a href="https://en.wikipedia.org/wiki/Transcriptome" target="_blank" class="query">transcriptome</a> from distinct cell (single cell resolution), and process hundreds of cells parallelly. Cellular heterogeneity can be largely reflected by cell's transcriptome, so sc-RNA-seq is a prominent technology to investigate cellular heterogeneity. sc-RNA-seq data can be used to constructing cell atlas.</p>
              </div>
              <div style="width: 100%; height: 15px"></div>
              <div class="col-3">
                <h4  class="subtitle">Why Zebrafish?</h4>
              </div>
              <div class="col-9">
                <p class=""><a href="https://en.wikipedia.org/wiki/Zebrafish" target="_blank" class="query"><span class="lead-emphasis">Z</span>ebrafish</a> is one of the Model Organism at biology, Human and Zebrafish has more than 80% similarity on genome. consequently, depicting molecular mechanism in zebrafish potentially enlightening research on human and medicine.</p>
                <p class="">The other reason for choosing zebrafish to build cell atlas is that zebrafish is more scalable compare to human. On contrast to human cell atlas which need labor from labs around the world, zebrafish is reachable one. One of aspect, for instance, is this smaller shape at anatomy which substantially need much lower cell coverage.</p>
                <p class="">To date, dozens of studies relate to zebrafish which apply sc-RNA-seq was published. Among these studies, some independently investigate a specific stage of embryonic development <a class="citation-index" v-on:click="toReference()">(3-5)</a>. Other studies obtain several samples from a developmental window (typically 0-24 hours) with designated sampling interval <a class="citation-index" v-on:click="toReference()">(6, 7)</a>. Because sc-RNA-seq is costly, these works either failed to screen long developmental window or designed a long sample interval which leads to low cell coverage.</p>
                <p class="content-emphasis">Overall, zebrafish is a valuable model for investigating cellular heterogeneity. Although different stage of zebrafish embryo has been study, a more comprehensive view of zebrafish developmental cell atlas is lacking.</p>
              </div>
              <div style="width: 100%; height: 15px"></div>
              <div class="col-3">
                <h4  class="subtitle">Data Availability</h4>
              </div>
              <div class="col-9">
                <p class=""><span class="lead-emphasis">T</span>otal 4 TB data was gathered, most of which is sc-RNA-seq data, after quality control -> mapping reads to reference genome -> counting, raw data was transform into a 25,000*990,000 (row*column) matrix, which row and column can refer to as features and samples respectively. Here is an illustration of the matrix:</p>
                <div class="fig-wrap">
                  <div class="pic-wrap" style="width: 600px"><img src="../assets/matrix.png"></div>
                  <div class="fig-comment">In the context of sc-RNA-seq data, features equivalent to genes and samples equivalent to cells.</div>
                </div>
                <p class="">Matrix data was persisted on my <a href="https://github.com/VonImt" target="_blank">github repository</a>. Raw data is too big to persist on serve (4 TB after compressing), so it is not provided online. Several state-of-the-art pipelines for mapping and counting procedure was run and evaluate consistence between their results. For who was interested in raw data or processing protocol, please contact me.</p>
              </div>
              <div style="width: 100%; height: 15px"></div>
              <div class="col-3">
                <h4  class="subtitle">Research Goals</h4>
              </div>
              <div class="col-9">
                <p class=""><span class="lead-emphasis">B</span>elow is some problems we can investigate from the matrix:</p>
                <ul>
                  <li>
                    <p>Evaluating Integrative Ability:</p>
                    <p>Because data are coming from different experience, we need a quantitative model to assess the power of batch effects and integrative ability between the data sets. Then we removing batch effects for further analysis.</p>
                  </li>
                  <li>
                    <p>Building Differential Trajectory:</p>
                    <p>The development of vertebrate embryo is a continuously differential procedure, a trajectory can be built to indicate this procedure, below is a demonstration of trajectory.</p>
                    <div class="fig-wrap">
                      <div class="pic-wrap" style="width: 500px"><img src="../assets/tra.png"></div>
                      <div class="fig-comment">Shape of a trajectory is similar to a growing tree, which root and branch can be recognized as stem cells and differential cells, each forks indicating a differential issue. As developmental time grows up, more distinctly differential cells can be observed (more branches).</div>
                    </div>
                    <p>Base on the trajectory, we can also identify which features (genes) are dominating at each specify branch.</p>
                  </li>
                  <li>
                    <p>database and visualization:</p>
                    <p>Outcomes of this research will organized as a database. We integrate visualization engine and other useful API into a tool kit, this tool kit can either access this database or processing data that come from other projects.</p>
                  </li>
                  <li>
                    <p><a href="https://en.wikipedia.org/wiki/Omics" target="_blank" class="query">Multi-omic</a> Analysis:</p>
                    <p>Sc-RNA-seq is a major method to dissect cellular heterogeneity, but there are three reasons that limited its capability to totally reflect cellular heterogeneity. First, RNA molecular amount within a cell is dynamic (also known as <a href="https://pubmed.ncbi.nlm.nih.gov/30089906/" target="_blank" class="query">RNA velocity</a>), but sc-RNA-seq technology only screen RNA profile at one specific time point. Second, a specific gene expresses high amount of RNA don not equivalent to express high amount of protein, because RNA typically undergoes <a href="https://en.wikipedia.org/wiki/Post-translational_modification" target="_blank" class="query">post-translational modification</a> and degrade procedures. Third, although sc-RNA-seq data can get RNA profile at single cell resolution, but it fails to reflect cell's original spatial location.</p>
                    <p>Overall, extra data is needed to promote the performance of sc-RNA-seq data. One of the prominent data type is spatial RNA sequencing data, starting from 2021, some technologies can obtain subcellular resolution <a class="citation-index" v-on:click="toReference()">(8, 9)</a>, making this kinds of technology has more exciting usage. For more discussion, please kindly refer to <a href="javascript:;" style="cursor: not-allowed">SpatioBorder</a>. </p>
                  </li>
                </ul>
              </div>
              <div style="width: 100%; height: 15px"></div>
              <div class="col-3">
                <h4  class="subtitle">Significance</h4>
              </div>
              <div class="col-9">
                <p><span class="lead-emphasis">T</span>aken together, our research offers unprecedently comprehensive view on development of zebrafish. Besides, we provide valuable resource for the single-cell community to develop and evaluate new strategies for an informative and interpretable cell atlas.</p>
              </div>
              <div style="width: 100%; height: 15px"></div>
              <div id="Reference" class="col-3">
                <h4  class="subtitle">Reference</h4>
              </div>
              <div class="col-9 citation-content" >
                <p>1.&nbsp;<span class="content-emphasis">Human Cell Atlas.</span> Home, https://www.humancellatlas.org/ (2021).</p>
                <p>2.&nbsp;Haniffa, M., Taylor, D., Linnarsson, S. et al. <span class="content-emphasis">A roadmap for the Human Developmental Cell Atlas. Nature</span> 597, 196–205 (2021). https://doi.org/10.1038/s41586-021-03620-1.</p>
                <p>3.&nbsp;Farnsworth DR, Saunders LM, Miller AC. <span class="content-emphasis">A single-cell transcriptome atlas for zebrafish development. Dev Biol.</span> 2020 Mar 15;459(2):100-108. doi: 10.1016/j.ydbio.2019.11.008. Epub 2019 Nov 27. PMID: 31782996; PMCID: PMC7080588.</p>
                <p>4.&nbsp;Metikala S, Casie Chetty S, Sumanas S. <span class="content-emphasis">Single-cell transcriptome analysis of the zebrafish embryonic trunk. PLoS One.</span> 2021 Jul 7;16(7):e0254024. doi: 10.1371/journal.pone.0254024. PMID: 34234366; PMCID: PMC8263256.</p>
                <p>5.&nbsp;Xia J, Kang Z, Xue Y, Ding Y, Gao S, Zhang Y, Lv P, Wang X, Ma D, Wang L, Han JJ, Liu F. <span class="content-emphasis">A single-cell resolution developmental atlas of hematopoietic stem and progenitor cell expansion in zebrafish. Proc Natl Acad Sci U S A.</span> 2021 Apr 6;118(14):e2015748118. doi: 10.1073/pnas.2015748118. PMID: 33785593; PMCID: PMC8040670.</p>
                <p>6.&nbsp;Farrell JA, Wang Y, Riesenfeld SJ, Shekhar K, Regev A, Schier AF. <span class="content-emphasis">Single-cell reconstruction of developmental trajectories during zebrafish embryogenesis. Science.</span> 2018 Jun 1;360(6392):eaar3131. doi: 10.1126/science.aar3131. Epub 2018 Apr 26. PMID: 29700225; PMCID: PMC6247916.</p>
                <p>7.&nbsp;Wagner DE, Weinreb C, Collins ZM, Briggs JA, Megason SG, Klein AM. <span class="content-emphasis">Single-cell mapping of gene expression landscapes and lineage in the zebrafish embryo. Science.</span> 2018 Jun 1;360(6392):981-987. doi: 10.1126/science.aar4362. Epub 2018 Apr 26. PMID: 29700229; PMCID: PMC6083445.</p>
                <p>8.&nbsp;Cho, C.S., Xi, J., Si, Y., Park, S.R., Hsu, J.E., Kim, M., Jun, G., Kang, H.M., and Lee, J.H. (2021a). <span class="content-emphasis">Microscopic examination of spatial transcriptome using Seq-Scope. Cell.</span> doi:10.1016/j.cell.2021.05.010</p>
                <p>9.&nbsp;Chen A, Liao S, Cheng M, Ma K, Wu L, Lai Y, Qiu X, Yang J, Xu J, Hao S, Wang X, Lu H, Chen X, Liu X, Huang X, Li Z, Hong Y, Jiang Y, Peng J, Liu S, Shen M, Liu C, Li Q, Yuan Y, Wei X, Zheng H, Feng W, Wang Z, Liu Y, Wang Z, Yang Y, Xiang H, Han L, Qin B, Guo P, Lai G, Muñoz-Cánoves P, Maxwell PH, Thiery JP, Wu QF, Zhao F, Chen B, Li M, Dai X, Wang S, Kuang H, Hui J, Wang L, Fei JF, Wang O, Wei X, Lu H, Wang B, Liu S, Gu Y, Ni M, Zhang W, Mu F, Yin Y, Yang H, Lisby M, Cornall RJ, Mulder J, Uhlén M, Esteban MA, Li Y, Liu L, Xu X, Wang J. <span class="content-emphasis">Spatiotemporal transcriptomic atlas of mouse organogenesis using DNA nanoball-patterned arrays. Cell.</span> 2022 May 12;185(10):1777-1792.e21. doi: 10.1016/j.cell.2022.04.003. Epub 2022 May 4. PMID: 35512705.</p>
              </div>
            </div>
          </div>
        </div>
