<script>
export default {
  name: 'portal',
  mounted () {
    window.addEventListener('scroll', this.handleScroll) // 监听滚动事件，然后用handleScroll这个方法进行相应的处理
  },
  data () {
    return {
      navActive: 'Home'
    }
  },
  methods: {
    changeActive (e) {
      this.navActive = e
      switch (e) {
        case 'Career':
          this.$el.querySelector('#Career').scrollIntoView({block: 'start'})
          break
        case 'Cell_Atlas' :
          this.$el.querySelector('#Cell_Atlas').scrollIntoView({block: 'start'})
          break
      }
    },
    handleScroll () {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      if (scrollTop < 700) {
        this.navActive = 'Home'
      } else if (scrollTop >= 700 && scrollTop < 1600) {
        this.navActive = 'Career'
      } else {
        this.navActive = 'Cell_Atlas'
      }
    },
    toReference () {
      this.$el.querySelector('#Reference').scrollIntoView({block: 'start'})
    }
  }
}
</script>

