import Vue from 'vue'
import Router from 'vue-router'
import portal from '@/components/portal'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'portal',
      component: portal
    }
  ]
  // base:'/Buhan_Feng/',
})



